.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
   }
 .about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
    transition: var(--transition);
 }
 .about__me-image{
    border-radius: 2rem;
    transform: rotate(10deg);
    overflow: hidden;
 }
 .about__me-image:hover{
    transform: rotate(0);
 }
 .about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr); /* check back to make grid changes */
    gap: 1.5rem;
 }
 
 .about__card{
    width: max-content;
    margin: 0 auto;
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
 }
 .about__card:hover{
    background: transparent;
    border: var(--color-primary-variant);
    cursor: pointer;
 }
 
 .about__icon{
    margin-bottom: 1rem;
    font-size: 1.4rem;
    color: var(--color-primary);
 }
 
 .about__card h5{
    font-size: 0.98rem;
    font-weight:bold;
 }
 .about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
 }
 
 .about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
 }
 
 
 /* ===== MEDIA QUERIES TABLET ===== */
 @media screen and (max-width: 1024px) {
   .about__container{
    grid-template-columns: 1fr;
    gap: 0;
   }
   .about__me{
    width: 50%;
    margin: 2rem auto 4rem;
   }
 
   .about__content p {
    margin: 1rem 0 1..5rem;
   }
 
  }
  
  /* ===== MEDIA QUERIES MOBILE ===== */
  @media screen and (max-width: 600px) {
    .about__me{
       width: 65%;
       margin: 0 auto 3rem;
    }
    .about__cards{
     display: block;
    }
 
    .about__card{
       width: 40vh;
       margin: 1rem auto;
    }
 
    .about__content{
       text-align: center;
    }
    .about__content p{
       margin: 1.5rem 0;
    }
  }